@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&display=swap');


* {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
    color: white;
}

html {
    height: 100%;
}

body::before {
    content: ""; /* Important */
    z-index: -1; /* Important */
    position: inherit;
    left: inherit;
    top: inherit;
    width: inherit;
    height: inherit;
    background-image: inherit;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    filter: blur(10px) grayscale(0.5) brightness(.5);
}

body {
    background-color: black;
    background-image: url("/img/back6.jpg");
    background-size: 0 0; /* Image should not be drawn here */
    width: 100vw;
    height: 100vh;
    position: fixed; /* Or absolute for scrollable backgrounds */
    overflow: auto;
    display: flex;
    flex-direction: column;
}


.main {
    font-family: 'Quicksand', sans-serif;
    flex: 1 0 auto;
    min-height: 100%;
    height: auto;
    flex-direction: column;
}

/*-------------- header------------------------*/
nav {
    background-color: rgba(0, 0, 0, .3);
    border-bottom: 1px solid rgba(255, 255, 255, .5);
}

.header-container {
    width: 70%;
    height: 3.75rem;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo p {
    font-size: 1.5rem;
    color: white;
    font-weight: 700;
    text-shadow: 2px 2px  #f5a83f;
}

.logo span {
    font-size: .73rem;
    color: #f5a83f;
}

.menu {
    display: flex;
}

.menu a {
    font-size: 1.2rem;
    transition: all 0.15s ease;
    margin-right: 1rem;
}

.menu a:hover {
    background-color: rgba(255, 255, 255, .2);
    padding: 1.2rem 0;
    color: #f5a83f;
}

.social a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 2.2rem;
    height: 2.2rem;
    font-size: 1.4rem;
    border-radius: 50%;
    transition: all .4s ease;
}


.social a:hover {
    background-color: rgba(255, 255, 255, .2);
    color: #f5a83f;
}

/*----------- content---------------------------*/

.projects-cont {
    overflow-y: auto;
    overflow-x: hidden;
    height: calc(100vh - 3.8rem);
}

.btn-projects {
    text-align: center;
    margin-bottom: 5rem;
}

.about-content {
    color: #fff;
    width: 70%;
    margin: 0 auto;
    font-size: 1.1rem;
    display: flex;
    justify-content: space-between;
}

.about-img img {
    max-width: 100%;
    border-radius: 50%;
    border: 2px solid #f5a83f;
}

.about-text {
    padding: 1rem;
}

.content-header {
    width: 70%;
    margin: 0 auto;
    text-align: center;
    padding: 1.8rem;
}

.content-header p {
    font-size: 1.5rem;
    color: #ccc;
}

.content-header span {
    color: #f5a83f;
}

.content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    width: 70%;
    margin: 1rem auto 5rem auto;
}

.box {
    padding: 5px;
    max-width: 300px;
}

.box p {
    color: white;
    text-align: center;
    padding: 5px;
    font-size: 1.2rem;
}

.box_footer {
    color: #d6d6d6;
    font-size: .9rem;
    padding: .5rem;
}

.box_footer a {
    color: #dff1ff;
    font-size: 1rem;
    margin-bottom: .5rem;
    display: inline-flex;
}

.box_footer a:hover {
    color: #f5a83f;
}


.box img {
    width: 300px;
    height: 200px;
    border-radius: 5px;
    max-width: 100%;
    cursor: pointer;
    filter: grayscale(.8);
    transition: all .4s ease;
}

.box img:hover {
    filter: grayscale(0);
    box-shadow: 0 0 1px 3px rgba(255, 255, 255, .2);
}

.iframe iframe {
    height: calc(100vh - 3.75rem);
    width: 100%;
    border: none;
}

.contacts-btn {
    display: flex;
    padding-top: 1.5rem;
    margin-bottom: 5rem;
}

.mr1 {
    margin-right: 1rem;
}

/*--------------scroll bar--------------*/
.projects-cont::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

.projects-cont::-webkit-scrollbar {
    width: 5px;
}

.projects-cont::-webkit-scrollbar-thumb {
    background-color: #b47e2e;
}

/*-----------text animation-----------*/

#container {
    color: white;
    font-size: 2.7rem;
    padding-top: 6rem;
    padding-bottom: 6rem;
    width: 100%;
    display: block;
    text-align: center;
}

#flip {
    height: 3.7rem;
    overflow: hidden;
}

#flip > div > div {
    color: #fff;
    padding: 4px 10px;
    height: 3.3rem;
    margin-bottom: 2.2rem;
    display: inline-block;
}

#flip div:first-child {
    animation: show 5s linear infinite;
}

#flip div div {
    background: #f5a83f;
    border-radius: 5px;
}

#flip div:first-child div {
    background: #4ec7f3;
    border-radius: 5px;
}

#flip div:last-child div {
    background: #DC143C;
    border-radius: 5px;
}

@keyframes show {
    0% {
        margin-top: -270px;
    }
    5% {
        margin-top: -180px;
    }
    33% {
        margin-top: -180px;
    }
    38% {
        margin-top: -90px;
    }
    66% {
        margin-top: -90px;
    }
    71% {
        margin-top: 0;
    }
    99.99% {
        margin-top: 0;
    }
    100% {
        margin-top: -270px;
    }
}

/*--------btn        ----------*/
.btn {
    outline: none;
    text-decoration: none;
    border-radius: 3rem;
    cursor: pointer;
    text-transform: uppercase;
    height: 3rem;
    width: 11rem;
    opacity: 1;
    background-color: transparent;
    border: 2px solid white;
}

.btn span {
    color: white;
    font-size: .8rem;
    letter-spacing: 1px;
}

.btn:hover {
    animation: rotate 0.7s ease-in-out both;
}

.btn:hover span {
    animation: storm 0.7s ease-in-out both;
    animation-delay: 0.06s;
    color: #fff;
}

.btn-about {
    outline: none;
    border-radius: 2rem;
    cursor: pointer;
    height: 2rem;
    width: 6rem;
    opacity: 1;
    background-color: transparent;
    border: 2px solid white;
}

.btn-about a {
    display: flex;
    justify-content: center;
    text-decoration: none;
    color: white;
    font-size: 1.2rem;
    letter-spacing: 1px;
    padding: .5rem;
}

.btn-about:hover {
    animation: rotate 0.7s ease-in-out both;
}

.btn-about:hover a {
    animation: storm 0.7s ease-in-out both;
    animation-delay: 0.06s;
    color: #fff;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg) translate3d(0, 0, 0);
    }
    25% {
        transform: rotate(3deg) translate3d(0, 0, 0);
    }
    50% {
        transform: rotate(-3deg) translate3d(0, 0, 0);
    }
    75% {
        transform: rotate(1deg) translate3d(0, 0, 0);
    }
    100% {
        transform: rotate(0deg) translate3d(0, 0, 0);
    }
}

@keyframes storm {
    0% {
        transform: translate3d(0, 0, 0) translateZ(0);
    }
    25% {
        transform: translate3d(4px, 0, 0) translateZ(0);
    }
    50% {
        transform: translate3d(-3px, 0, 0) translateZ(0);
    }
    75% {
        transform: translate3d(2px, 0, 0) translateZ(0);
    }
    100% {
        transform: translate3d(0, 0, 0) translateZ(0);
    }
}
