/*---------- media query---------------*/

@media screen and (max-width: 1024px) {
    nav {
        padding: 0 10px;
    }

    .header-container {
        width: 100%;
    }

    .about-content {
        width: 100%;
    }

    .content-header {
        width: 100%;
    }

    .content {
        width: 100%;
    }
}

@media screen and (max-width: 768px) {
    .box {
        max-width: 500px;
    }

    .box img {
        width: 500px;
        height: auto;
        filter: none;
        transition: none;
    }

    .box img:hover {
        filter: none;
        box-shadow: none;
    }

    .content-header {
        padding: .5rem;
    }

    .about-content {
        font-size: 1rem;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .about-img {
        text-align: center;
        padding: 1rem 0;
    }

    .about-img img {
        max-width: 100%;
        width: 50%;
    }

    .contacts-btn {
        justify-content: center;
    }
}

@media screen and (max-width: 620px) {
    .projects-cont {
        height: calc(100vh - 6rem);
    }

    .iframe iframe {
        height: calc(100vh - 6rem);
    }

    .header-container {
        height: 6rem;
        flex-direction: column;
    }

    .logo p {
        font-size: 1.3rem;
        letter-spacing: 5px;
        position: relative;
    }

    .logo span {
        font-size: .52rem;
        position: absolute;
        margin-top: -2px;
    }

    .menu a {
        font-size: 1.1rem;
    }

    .menu a:hover {
        background-color: transparent;
        padding: 0;
    }

    .social a:hover {
        background-color: transparent;
    }

    .social a {
        font-size: 1rem;
        height: 1.5rem;
    }

    .content-header {
        padding: 0;
        margin-top: 2rem;
    }

    .btn-about {
        border-radius: 1rem;
        height: 1.5rem;
        width: 3rem;
    }

    .btn-about a {
        font-size: 1rem;
        padding: .25rem;
    }
}

@media screen and (max-width: 576px) {
    .content-header p {
        font-size: 1rem;
    }

    .about-text {
        font-size: .9rem;
    }

    #container {
        font-size: 1.5rem;
    }

    #flip {
        height: 2.2rem;
    }

    #flip > div > div {
        height: 2.8rem;
        margin-bottom: 2.8rem;
    }
}
